@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

:root {
  --primary: #3b82f6;
  --primary-dark: #2563eb;
  --text-dark: #1f2937;
  --text-light: #6b7280;
  --bg-light: #f9fafb;
  --bg-white: #ffffff;
  --border: #e5e7eb;
  --transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', sans-serif;
  color: var(--text-dark);
  background: var(--bg-white);
}

.modern-landing-page {
  width: 100%;
  overflow-x: hidden;
}

/* Navbar */
.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(40px);
  border-bottom: 2px solid rgba(59, 130, 246, 0.1);
  z-index: 999;
  padding: 0.8rem 0;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.08);
  transition: var(--transition);
}

.navbar-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-logo {
  display: flex;
  align-items: center;
  gap: 0.6rem;
  font-size: 1.35rem;
  font-weight: 900;
  background: linear-gradient(135deg, var(--primary), #2563eb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  cursor: pointer;
  transition: var(--transition);
  letter-spacing: -0.5px;
}

.navbar-logo:hover {
  transform: scale(1.06);
  filter: drop-shadow(0 6px 16px rgba(59, 130, 246, 0.45));
}

.navbar-menu {
  display: flex;
  gap: 3rem;
  align-items: center;
}

.nav-link {
  color: var(--text-light);
  text-decoration: none;
  font-weight: 600;
  transition: var(--transition);
  position: relative;
  padding: 8px 14px;
  border-radius: 8px;
  font-size: 14px;
  letter-spacing: 0.2px;
}

.nav-link:hover {
  color: var(--primary);
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.1), rgba(59, 130, 246, 0.05));
}

.nav-link::after {
  content: '';
  position: absolute;
  bottom: 4px;
  left: 14px;
  width: 0;
  height: 2.5px;
  background: linear-gradient(90deg, var(--primary), var(--primary-dark));
  transition: width 0.5s cubic-bezier(0.34, 1.56, 0.64, 1);
  border-radius: 2px;
}

.nav-link:hover::after {
  width: calc(100% - 28px);
}

.navbar-buttons {
  display: flex;
  gap: 1rem;
}

.nav-btn, .nav-btn-filled {
  border-radius: 12px;
  font-weight: 700;
  transition: var(--transition);
  height: 46px;
  padding: 0 2rem;
  font-size: 14px;
  letter-spacing: 0.4px;
  cursor: pointer;
}

.nav-btn {
  border: 2.5px solid var(--primary);
  color: var(--primary);
  background: white;
}

.nav-btn:hover {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.08), rgba(59, 130, 246, 0.03));
  border-color: var(--primary-dark);
  transform: translateY(-3px);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.3);
}

.nav-btn-filled {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  border: none;
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.4);
}

.nav-btn-filled:hover {
  background: linear-gradient(135deg, var(--primary-dark), #1e40af);
  transform: translateY(-4px);
  box-shadow: 0 16px 36px rgba(59, 130, 246, 0.5);
}

.nav-btn-filled:active {
  transform: translateY(-1px);
}

.mobile-menu-btn {
  display: none;
  background: none;
  border: none;
  color: var(--primary);
  font-size: 1.5rem;
  cursor: pointer;
}

/* Hero Section */
.hero-section {
  margin-top: 70px;
  padding: 5rem 2rem;
  background: linear-gradient(180deg, #ffffff 0%, #f8fafc 50%, #f0f4f8 100%);
  min-height: calc(100vh - 70px);
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.hero-background-shapes {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
}

.shape {
  position: absolute;
  border-radius: 50%;
  opacity: 0.08;
  animation: float 8s ease-in-out infinite;
}

.shape-1 {
  width: 400px;
  height: 400px;
  background: var(--primary);
  top: -100px;
  left: -100px;
  animation-delay: 0s;
}

.shape-2 {
  width: 300px;
  height: 300px;
  background: #60a5fa;
  top: 50%;
  right: 5%;
  animation-delay: 1s;
}

.shape-3 {
  width: 250px;
  height: 250px;
  background: var(--primary);
  bottom: -50px;
  left: 10%;
  animation-delay: 2s;
}

.hero-content {
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  position: relative;
  z-index: 1;
}

.hero-text {
  animation: fadeInUp 0.8s ease-out;
}

.hero-badge {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.12), rgba(59, 130, 246, 0.06));
  border: 2px solid rgba(59, 130, 246, 0.35);
  padding: 10px 20px;
  border-radius: 30px;
  font-size: 14px;
  font-weight: 700;
  color: var(--primary);
  margin-bottom: 2rem;
  animation: slideInLeft 0.6s ease-out;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 15px rgba(59, 130, 246, 0.1);
}

.hero-title {
  font-size: 4.5rem;
  font-weight: 900;
  line-height: 1.1;
  margin-bottom: 1.5rem;
  color: var(--text-dark);
  letter-spacing: -1.5px;
  animation: fadeInUp 0.8s ease-out 0.2s both;
  word-spacing: 0.1em;
}

.typing-text {
  background: linear-gradient(135deg, var(--primary), #2563eb);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 900;
}

.typing-cursor {
  display: inline-block;
  width: 3px;
  height: 1em;
  background: var(--primary);
  margin-left: 8px;
  animation: blink 1s infinite;
}

@keyframes blink {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0; }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.hero-subtitle {
  font-size: 1.2rem;
  color: #4b5563;
  margin-bottom: 3rem;
  line-height: 1.8;
  max-width: 580px;
  animation: fadeInUp 0.8s ease-out 0.3s both;
  font-weight: 500;
  letter-spacing: 0.3px;
}

.hero-buttons {
  display: flex;
  gap: 1.5rem;
  flex-wrap: wrap;
  animation: fadeInUp 0.8s ease-out 0.4s both;
}

.btn-get-started, .btn-learn-more {
  border-radius: 14px;
  font-weight: 700;
  font-size: 16px;
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  transition: var(--transition);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.2);
  letter-spacing: 0.4px;
}

.btn-get-started {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  border: none;
  padding: 0 2.8rem;
}

.btn-get-started:hover {
  transform: translateY(-6px);
  box-shadow: 0 20px 48px rgba(59, 130, 246, 0.4);
  background: linear-gradient(135deg, var(--primary-dark), #1e40af);
}

.btn-get-started:active {
  transform: translateY(-2px);
}

.btn-learn-more {
  border: 2.5px solid var(--primary);
  color: var(--primary);
  background: white;
  padding: 0 2.8rem;
}

.btn-learn-more:hover {
  background: rgba(59, 130, 246, 0.08);
  border-color: var(--primary-dark);
  transform: translateY(-6px);
  box-shadow: 0 20px 48px rgba(59, 130, 246, 0.25);
}

.btn-learn-more:active {
  transform: translateY(-2px);
}

.hero-image {
  display: flex;
  justify-content: center;
  align-items: center;
  animation: fadeInRight 0.8s ease-out;
  height: 450px;
  position: relative;
}

.hero-image-asset {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 20px;
  box-shadow: 0 20px 60px rgba(59, 130, 246, 0.25);
  animation: fadeInUp 0.8s ease-out 0.2s both;
  filter: drop-shadow(0 0 30px rgba(59, 130, 246, 0.15));
  transition: var(--transition);
  position: relative;
  z-index: 2;
}

.hero-image-asset:hover {
  transform: translateY(-8px);
  box-shadow: 0 28px 72px rgba(59, 130, 246, 0.35);
  filter: drop-shadow(0 0 40px rgba(59, 130, 246, 0.25));
}

/* Features Section */
.features-section {
  padding: 6rem 2rem;
  background: var(--bg-white);
}

.features-container {
  max-width: 1200px;
  margin: 0 auto;
}

.features-title {
  font-size: 2.5rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 0.5rem;
  color: var(--text-dark);
}

.features-subtitle {
  text-align: center;
  color: var(--text-light);
  margin-bottom: 3.5rem;
  font-size: 1.1rem;
}

.features-carousel {
  display: flex;
  align-items: center;
  gap: 2rem;
  margin-bottom: 3rem;
  position: relative;
}

.carousel-nav {
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px solid var(--primary);
  background: white;
  color: var(--primary);
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  transition: var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
}

.carousel-nav:hover {
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  transform: scale(1.1);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.3);
}

.carousel-track {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  min-height: 340px;
}

.carousel-item {
  animation: fadeInUp 0.5s ease-out;
}

.feature-card {
  padding: 0;
  border: none;
  background: transparent;
  text-align: center;
  cursor: pointer;
  transition: var(--transition);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.feature-card-inner {
  background: linear-gradient(135deg, #ffffff 0%, rgba(59, 130, 246, 0.03) 100%);
  border: 2px solid rgba(59, 130, 246, 0.15);
  border-radius: 16px;
  padding: 2.5rem 2rem;
  transition: var(--transition);
  position: relative;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.feature-card:hover .feature-card-inner {
  border-color: var(--primary);
  background: linear-gradient(135deg, #ffffff 0%, rgba(59, 130, 246, 0.08) 100%);
  box-shadow: 0 20px 40px rgba(59, 130, 246, 0.2);
  transform: translateY(-12px);
}

.feature-card-accent {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, var(--primary), var(--primary-dark));
  transform: scaleX(0);
  transition: transform 0.5s ease;
  transform-origin: left;
}

.feature-card:hover .feature-card-accent {
  transform: scaleX(1);
}

.feature-icon {
  font-size: 3.5rem;
  margin-bottom: 1.5rem;
  display: inline-block;
  transition: var(--transition);
}

.feature-card:hover .feature-icon {
  transform: scale(1.15) rotate(5deg);
}

.feature-card h3 {
  font-size: 1.35rem;
  font-weight: 800;
  margin-bottom: 1rem;
  color: var(--text-dark);
  letter-spacing: -0.3px;
}

.feature-card p {
  color: var(--text-light);
  font-size: 0.95rem;
  line-height: 1.7;
  margin: 0;
}

.carousel-indicators {
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 2rem;
}

.indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid var(--primary);
  background: white;
  cursor: pointer;
  transition: var(--transition);
}

.indicator.active {
  background: var(--primary);
  transform: scale(1.3);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.4);
}

.indicator:hover {
  box-shadow: 0 2px 8px rgba(59, 130, 246, 0.3);
}

/* Benefits Section */
.benefits-section {
  padding: 6rem 2rem;
  background: var(--bg-light);
}

.benefits-container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-title {
  font-size: 2.5rem;
  font-weight: 800;
  text-align: center;
  margin-bottom: 3.5rem;
  color: var(--text-dark);
}

.benefits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2.5rem;
}

.benefit-item {
  background: var(--bg-white);
  padding: 2rem;
  border-radius: 12px;
  border: 1px solid var(--border);
  transition: var(--transition);
  text-align: center;
}

.benefit-item:hover {
  border-color: var(--primary);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.08);
}

.benefit-icon {
  font-size: 2rem;
  color: var(--primary);
  margin-bottom: 1rem;
}

.benefit-item h3 {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.75rem;
  color: var(--text-dark);
}

.benefit-item p {
  color: var(--text-light);
  font-size: 0.95rem;
  line-height: 1.6;
}

/* Workflow/Flowchart Section */
.workflow-section {
  padding: 6rem 2rem;
  background: linear-gradient(180deg, #f0f9ff 0%, var(--bg-white) 100%);
}

.workflow-container {
  max-width: 1200px;
  margin: 0 auto;
}

.section-subtitle {
  text-align: center;
  color: var(--text-light);
  margin-bottom: 3rem;
  font-size: 1.1rem;
}

.flowchart {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 2.5rem;
  margin-bottom: 4rem;
  padding: 3.5rem 2rem;
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.12) 0%, rgba(59, 130, 246, 0.04) 50%, rgba(34, 197, 94, 0.04) 100%);
  border-radius: 24px;
  border: 2px solid rgba(59, 130, 246, 0.2);
  position: relative;
  box-shadow: inset 0 2px 8px rgba(59, 130, 246, 0.05);
}

.flowchart::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent 0%, rgba(59, 130, 246, 0.03) 50%, transparent 100%);
  border-radius: 24px;
  pointer-events: none;
}

.flowchart-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  position: relative;
  animation: fadeInUp 0.6s ease-out;
}

.flowchart-item:nth-child(1) { animation-delay: 0.1s; }
.flowchart-item:nth-child(2) { animation-delay: 0.2s; }
.flowchart-item:nth-child(3) { animation-delay: 0.3s; }
.flowchart-item:nth-child(4) { animation-delay: 0.4s; }
.flowchart-item:nth-child(5) { animation-delay: 0.5s; }

.flowchart-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 14px;
  background: linear-gradient(135deg, #ffffff 0%, rgba(59, 130, 246, 0.03) 100%);
  padding: 28px 24px;
  border-radius: 18px;
  border: 2px solid var(--border);
  transition: var(--transition);
  cursor: pointer;
  width: 100%;
  text-align: center;
  box-shadow: 0 4px 16px rgba(59, 130, 246, 0.08);
  position: relative;
  overflow: hidden;
}

.flowchart-step::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(59, 130, 246, 0.2), transparent);
  transition: left 0.5s ease;
}

.flowchart-step:hover::before {
  left: 100%;
}

.flowchart-step:hover {
  border-color: var(--primary);
  box-shadow: 0 16px 40px rgba(59, 130, 246, 0.25);
  transform: translateY(-12px);
  background: linear-gradient(135deg, #ffffff 0%, rgba(59, 130, 246, 0.08) 100%);
}

.flowchart-step:active {
  transform: translateY(-8px);
}

.step-icon {
  font-size: 44px;
  flex-shrink: 0;
  transition: var(--transition);
}

.flowchart-step:hover .step-icon {
  transform: scale(1.15) rotate(5deg);
}

.step-label {
  text-align: center;
  width: 100%;
  position: relative;
  z-index: 1;
}

.step-number {
  font-size: 11px;
  font-weight: 900;
  color: var(--primary);
  text-transform: uppercase;
  letter-spacing: 1.5px;
  display: block;
  margin-bottom: 8px;
  opacity: 0.8;
}

.step-label p {
  margin: 0;
  font-weight: 700;
  color: var(--text-dark);
  font-size: 16px;
  letter-spacing: -0.3px;
}

.flowchart-arrow {
  font-size: 32px;
  color: var(--primary);
  margin: 0;
  font-weight: 700;
  animation: slideArrow 3s cubic-bezier(0.4, 0, 0.2, 1) infinite;
  opacity: 0.8;
  filter: drop-shadow(0 2px 4px rgba(59, 130, 246, 0.2));
}

.flowchart-item:last-child .flowchart-arrow {
  display: none;
}

@keyframes slideArrow {
  0%, 100% { transform: translateX(0) scale(1); }
  50% { transform: translateX(8px) scale(1.1); }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.flowchart-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2.5rem;
  margin-top: 3.5rem;
  animation: fadeInUp 0.8s ease-out 0.2s both;
}

.detail-box {
  position: relative;
  background: linear-gradient(135deg, #ffffff 0%, rgba(59, 130, 246, 0.04) 100%);
  padding: 2.2rem;
  border-radius: 16px;
  border: 2px solid rgba(59, 130, 246, 0.12);
  box-shadow: 0 4px 16px rgba(59, 130, 246, 0.08);
  transition: var(--transition);
  overflow: hidden;
}

.detail-box::before {
  content: '';
  position: absolute;
  top: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, transparent, var(--primary), transparent);
  transform: scaleX(0);
  transition: transform 0.5s ease;
  transform-origin: left;
}

.detail-box:hover::before {
  transform: scaleX(1);
}

.detail-box::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(59, 130, 246, 0.1), transparent);
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.detail-box:hover::after {
  opacity: 1;
}

.detail-box:hover {
  border-color: var(--primary);
  box-shadow: 0 12px 32px rgba(59, 130, 246, 0.2);
  transform: translateY(-8px);
}

.detail-box h4 {
  font-size: 17px;
  font-weight: 800;
  color: var(--text-dark);
  margin-bottom: 12px;
  position: relative;
  z-index: 1;
  letter-spacing: -0.3px;
}

.detail-box p {
  font-size: 14px;
  color: var(--text-light);
  margin: 0;
  line-height: 1.7;
  position: relative;
  z-index: 1;
}

/* Roles Section */
.roles-section {
  padding: 6rem 2rem;
  background: var(--bg-light);
}

.roles-container {
  max-width: 1200px;
  margin: 0 auto;
}

.roles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 3rem;
}

.role-card {
  padding: 2.5rem;
  border-radius: 12px;
  border: 2px solid var(--border);
  background: var(--bg-white);
  transition: var(--transition);
  text-align: center;
}

.role-card:hover {
  border-color: var(--primary);
  background: var(--bg-white);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.1);
}

.role-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  display: inline-block;
}

.role-card h3 {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: var(--text-dark);
}

.role-card ul {
  list-style: none;
  text-align: left;
}

.role-card li {
  padding: 0.75rem 0;
  color: var(--text-light);
  font-size: 0.95rem;
  border-bottom: 1px solid var(--border);
  position: relative;
  padding-left: 20px;
}

.role-card li:before {
  content: '✓';
  position: absolute;
  left: 0;
  color: var(--primary);
  font-weight: 700;
}

.role-card li:last-child {
  border-bottom: none;
}

/* FAQ Section */
.faq-section {
  padding: 6rem 2rem;
  background: var(--bg-white);
}

.faq-container {
  max-width: 900px;
  margin: 0 auto;
}

.accordion-wrapper {
  margin-top: 3rem;
}

.custom-accordion .ant-collapse-header {
  font-weight: 600 !important;
  color: var(--text-dark) !important;
  font-size: 15px !important;
  padding: 16px 20px !important;
}

.custom-accordion .ant-collapse-item {
  border: 1px solid var(--border) !important;
  border-radius: 8px !important;
  margin-bottom: 12px !important;
  overflow: hidden;
}

.custom-accordion .ant-collapse-item:last-child {
  margin-bottom: 0 !important;
}

.custom-accordion .ant-collapse-content-box {
  padding: 16px 20px !important;
  background: var(--bg-light);
}

.custom-accordion .ant-collapse-header:hover {
  color: var(--primary) !important;
}

.custom-accordion .ant-collapse-item-active .ant-collapse-header {
  color: var(--primary) !important;
}

/* CTA Section */
.cta-section {
  padding: 6rem 2rem;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  text-align: center;
}

.cta-container {
  max-width: 800px;
  margin: 0 auto;
}

.cta-section h2 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1rem;
}

.cta-section p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  opacity: 0.95;
}

.cta-button {
  background: white;
  color: var(--primary);
  border: none;
  font-weight: 700;
  padding: 0 2rem;
  height: 48px;
  border-radius: 8px;
  font-size: 1rem;
}

.cta-button:hover {
  background: var(--bg-light);
  transform: translateY(-2px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
}

/* Premium Footer */
.premium-footer {
  background: linear-gradient(180deg, #0f172a 0%, #1a2332 50%, #142341 100%);
  color: white;
  padding: 0;
  border-top: 3px solid var(--primary);
  position: relative;
  overflow: hidden;
}

.premium-footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 50%, rgba(59, 130, 246, 0.1) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(59, 130, 246, 0.08) 0%, transparent 50%);
  pointer-events: none;
}

.footer-top-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent 0%, rgba(59, 130, 246, 0.3) 50%, transparent 100%);
}

.footer-main-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4.5rem 2rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 3.5rem;
  position: relative;
  z-index: 1;
}

/* Brand Section */
.footer-brand-section {
  grid-column: 1;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 22px;
  font-weight: 800;
  margin-bottom: 1.5rem;
  color: var(--primary);
}

.footer-logo span {
  background: linear-gradient(135deg, var(--primary), #60a5fa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.footer-brand-desc {
  color: rgba(255, 255, 255, 0.75);
  font-size: 14px;
  line-height: 1.8;
  margin-bottom: 1.8rem;
  max-width: 280px;
}

.footer-social-icons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.footer-social-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 42px;
  height: 42px;
  background: rgba(59, 130, 246, 0.15);
  border: 2px solid rgba(59, 130, 246, 0.3);
  border-radius: 50%;
  color: white;
  text-decoration: none;
  transition: var(--transition);
  font-weight: 700;
  font-size: 16px;
}

.footer-social-icon:hover {
  background: var(--primary);
  border-color: var(--primary);
  transform: translateY(-4px) rotate(5deg);
  box-shadow: 0 12px 24px rgba(59, 130, 246, 0.4);
}

/* Footer Columns */
.footer-col {
  position: relative;
}

.footer-col h5 {
  font-size: 12px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: white;
  margin-bottom: 1.8rem;
  padding-bottom: 0.8rem;
  border-bottom: 2px solid rgba(59, 130, 246, 0.3);
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 1rem;
  overflow: hidden;
}

.footer-links a {
  color: rgba(255, 255, 255, 0.75);
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  transition: var(--transition);
  position: relative;
  display: inline-block;
}

.footer-links a::before {
  content: '›';
  position: absolute;
  left: -20px;
  color: var(--primary);
  font-weight: 700;
  transition: left 0.3s ease;
}

.footer-links a:hover {
  color: var(--primary);
  padding-left: 8px;
}

.footer-links a:hover::before {
  left: 0;
}

/* Stats Section */
.footer-stats-section h5 {
  font-size: 12px;
  font-weight: 800;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  color: white;
  margin-bottom: 1.8rem;
  padding-bottom: 0.8rem;
  border-bottom: 2px solid rgba(59, 130, 246, 0.3);
}

.footer-stats-grid {
  display: grid;
  gap: 1.5rem;
}

.footer-stat-item {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.1), rgba(59, 130, 246, 0.05));
  padding: 1.5rem;
  border-radius: 12px;
  border: 1px solid rgba(59, 130, 246, 0.2);
  transition: var(--transition);
  text-align: center;
}

.footer-stat-item:hover {
  background: linear-gradient(135deg, rgba(59, 130, 246, 0.15), rgba(59, 130, 246, 0.08));
  border-color: var(--primary);
  transform: translateY(-4px);
  box-shadow: 0 8px 20px rgba(59, 130, 246, 0.2);
}

.footer-stat-value {
  font-size: 24px;
  font-weight: 800;
  color: var(--primary);
  line-height: 1;
}

.footer-stat-label {
  font-size: 12px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.7);
  margin-top: 6px;
  letter-spacing: 0.5px;
  font-weight: 600;
}

/* Footer Bottom */
.footer-bottom-divider {
  height: 1px;
  background: linear-gradient(90deg, transparent 0%, rgba(59, 130, 246, 0.3) 50%, transparent 100%);
}

.footer-bottom-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
  position: relative;
  z-index: 1;
}

.footer-copyright {
  color: rgba(255, 255, 255, 0.65);
  font-size: 13px;
  margin: 0;
  font-weight: 500;
}

.footer-badges {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.footer-badge {
  display: inline-block;
  padding: 6px 14px;
  background: rgba(59, 130, 246, 0.15);
  border: 1px solid rgba(59, 130, 246, 0.3);
  border-radius: 20px;
  color: rgba(255, 255, 255, 0.85);
  font-size: 12px;
  font-weight: 600;
  transition: var(--transition);
}

.footer-badge:hover {
  background: var(--primary);
  border-color: var(--primary);
  color: white;
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(30px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

/* Responsive */
@media (max-width: 768px) {
  .navbar-container {
    padding: 0 1rem;
  }

  .navbar-menu {
    display: none;
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    flex-direction: column;
    background: white;
    border-bottom: 1px solid var(--border);
    padding: 1rem;
    gap: 1rem;
  }

  .navbar-menu.active {
    display: flex;
  }

  .mobile-menu-btn {
    display: block;
  }

  .navbar-buttons {
    display: none;
  }

  .hero-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .hero-title {
    font-size: 2.5rem;
    font-weight: 900;
  }

  .hero-subtitle {
    font-size: 1.05rem;
  }

  .hero-image {
    height: 300px;
  }

  .features-carousel {
    gap: 1rem;
    margin-bottom: 2rem;
  }

  .carousel-nav {
    width: 44px;
    height: 44px;
    font-size: 22px;
  }

  .carousel-track {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    min-height: 320px;
  }

  .feature-card-inner {
    padding: 2rem 1.5rem;
  }

  .feature-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
  }

  .feature-card h3 {
    font-size: 1.15rem;
  }

  .features-title,
  .section-title {
    font-size: 2rem;
  }

  .flowchart {
    flex-direction: column;
    padding: 2rem;
  }

  .flowchart-arrow {
    margin: 8px 0;
    transform: rotate(90deg);
  }

  .flowchart-details {
    grid-template-columns: 1fr;
  }

  .footer-stats {
    gap: 1.5rem;
  }
}

@media (max-width: 480px) {
  .hero-section {
    padding: 4rem 1.5rem;
  }

  .hero-title {
    font-size: 2rem;
    font-weight: 900;
    margin-bottom: 1rem;
  }

  .hero-subtitle {
    font-size: 0.95rem;
  }

  .hero-buttons {
    flex-direction: column;
    width: 100%;
    gap: 1rem;
  }

  .btn-get-started,
  .btn-learn-more {
    width: 100%;
    height: 48px;
    font-size: 15px;
  }

  .hero-image {
    height: 250px;
  }

  .features-carousel {
    flex-direction: column;
    gap: 1rem;
  }

  .carousel-nav {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }

  .carousel-track {
    grid-template-columns: 1fr;
    gap: 1.5rem;
    min-height: auto;
  }

  .feature-card-inner {
    padding: 1.5rem 1.2rem;
  }

  .feature-icon {
    font-size: 2.5rem;
    margin-bottom: 0.8rem;
  }

  .feature-card h3 {
    font-size: 1rem;
    margin-bottom: 0.5rem;
  }

  .feature-card p {
    font-size: 0.85rem;
  }

  .carousel-indicators {
    gap: 6px;
    margin-top: 1.5rem;
  }

  .indicator {
    width: 8px;
    height: 8px;
  }

  .features-title,
  .section-title {
    font-size: 1.5rem;
  }

  .feature-card,
  .benefit-item,
  .role-card {
    padding: 1.5rem;
  }

  .navbar-logo {
    font-size: 1.25rem;
  }

  .cta-section h2 {
    font-size: 1.75rem;
  }

  .flowchart-step {
    flex-direction: column;
    text-align: center;
  }

  .detail-box {
    padding: 1rem;
  }
}

/* Feature Card Animation */
.feature-card {
  opacity: 0;
  animation: fadeInUp 0.6s ease-out forwards;
}

.feature-card:nth-child(1) { animation-delay: 0.1s; }
.feature-card:nth-child(2) { animation-delay: 0.2s; }
.feature-card:nth-child(3) { animation-delay: 0.3s; }
.feature-card:nth-child(4) { animation-delay: 0.4s; }
.feature-card:nth-child(5) { animation-delay: 0.5s; }
.feature-card:nth-child(6) { animation-delay: 0.6s; }
